body {
  margin: 0;
  padding: 0;
  font-family: "TT Norms", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active),
input[type=checkbox].visually-hidden,
input[type=radio].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.callback-btn {
  padding: 18px 40px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #ffc464;
  background-color: transparent;
  border: 2px solid #ffc464;
  border-radius: 15px;
  cursor: pointer;
  transition: 0.5s ease;
}
.callback-btn:hover {
  border-color: #ffffff;
  color: #ffffff;
}

.title {
  margin: 0;
  font-weight: 600;
  font-size: 38px;
  line-height: 36px;
  color: #ffffff;
}

@font-face {
  font-weight: 400;
  font-family: "TT Norms";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/TTNorms-Regular.woff2") format("woff2"), url("../fonts/TTNorms-Regular.woff") format("woff");
}
@font-face {
  font-weight: 500;
  font-family: "TT Norms";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/TTNorms-Medium.woff2") format("woff2"), url("../fonts/TTNorms-Medium.woff") format("woff");
}
@font-face {
  font-weight: 700;
  font-family: "TT Norms";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/TTNorms-Bold.woff2") format("woff2"), url("../fonts/TTNorms-Bold.woff") format("woff");
}
@font-face {
  font-weight: 800;
  font-family: "TT Norms";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/TTNorms-ExtraBold.woff2") format("woff2"), url("../fonts/TTNorms-ExtraBold.woff") format("woff");
}
@keyframes arrow-move {
  0% {
    transform: translateX(0px);
  }
  25% {
    transform: translateX(5px);
  }
  50% {
    transform: translateX(0px);
  }
  75% {
    transform: translateX(5px);
  }
  100% {
    transform: translateX(0px);
  }
}
@keyframes blinking {
  0% {
    opacity: 1;
  }
  12% {
    opacity: 0.75;
  }
  25% {
    opacity: 0.5;
  }
  37% {
    opacity: 0.25;
  }
  50% {
    opacity: 0;
  }
  63% {
    opacity: 0.25;
  }
  75% {
    opacity: 0.5;
  }
  88% {
    opacity: 0.75;
  }
  100% {
    opacity: 1;
  }
}
.page-header {
  background-color: #a95094;
}

.page-header__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  padding: 25px 0 0 30px;
  box-sizing: border-box;
}

.page-header__menu {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #994786;
}
.page-header__menu::before {
  position: absolute;
  bottom: -1px;
  right: 0;
  width: 30px;
  height: 4px;
  content: "";
  background-color: #a95094;
}
.page-header__menu::after {
  position: absolute;
  top: 0;
  right: 0;
  width: 10%;
  height: 100%;
  content: "";
  pointer-events: none;
  background: linear-gradient(to left, #a95094 0, rgba(169, 80, 148, 0.1) 100%);
}

.page-header__city {
  position: relative;
  padding-left: 30px;
  margin-right: 35px;
  color: #ffffff;
  transition: 0.5s ease;
}
.page-header__city::before {
  position: absolute;
  left: 0;
  width: 20px;
  height: 20px;
  content: "";
  background-image: url(../img/icon-geo.svg);
}
.page-header__city:hover {
  color: #ffc464;
}

.page-header__logo {
  margin-right: auto;
}

.page-header__callback-btn {
  margin-right: 30px;
}

@media (min-width: 780px) {
  .page-header__wrapper {
    padding: 20px 0 0;
  }
  .page-header__menu {
    justify-content: space-between;
  }
  .page-header__menu::before {
    display: none;
  }
  .page-header__menu::after {
    display: none;
  }
  .page-header__city {
    margin-left: 30px;
  }
  .page-header__logo {
    margin-left: 30px;
  }
}
@media (min-width: 1440px) {
  .page-header__wrapper {
    padding: 30px 135px 0;
    align-items: baseline;
    width: 1440px;
    margin: 0 auto;
  }
  .page-header__main-nav {
    margin-right: 160px;
  }
  .page-header__menu {
    width: initial;
    margin-bottom: 0;
    order: 2;
    justify-content: space-between;
    border-bottom: none;
  }
  .page-header__logo {
    margin-left: 0;
    margin-right: 75px;
    order: 1;
    align-self: center;
  }
  .page-header__city {
    margin-left: 0;
    order: 1;
  }
  .page-header__callback-btn {
    margin-right: 0;
    order: 3;
  }
}
.page-main__repair {
  background-color: #a95094;
}

.page-footer {
  display: flex;
  flex-direction: column;
  padding: 75px 0 20px;
}

.page-footer__logo {
  margin-bottom: 35px;
  text-align: center;
}

.page-footer__nav {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 50px;
  padding: 0 45px;
  gap: 15px 40px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}

.page-footer__nav-link {
  color: #000000;
}

.page-footer__payment-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 35px 0;
  margin-bottom: 35px;
  background-color: #fafafa;
}

.page-footer__subtitle {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}

.page-footer__payments-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px 17px;
  margin: 0 35px;
}

.page-footer__payments-item {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 92px;
  height: 50px;
  background-color: #ffffff;
}

.page-footer__payment-text {
  font-weight: 500;
  font-size: 14px;
  line-height: 26px;
}

.page-footer__company-detail {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-footer__company-detail-item {
  margin-bottom: 10px;
}

@media (min-width: 780px) {
  .page-footer__company-detail {
    padding: 0 30px;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1440px) {
  .page-footer {
    width: 1440px;
    margin: 0 auto;
  }
  .page-footer__nav-link {
    transition: 0.5s ease;
  }
  .page-footer__nav-link:hover {
    color: #a95094;
  }
  .page-footer__payment-container {
    flex-direction: row;
    justify-content: center;
    width: 1338px;
    margin: 0 auto 35px;
  }
  .page-footer__company-detail {
    width: 1186px;
    padding: 0;
    margin: 0 auto;
  }
}
.logo__img {
  width: 110px;
  height: 32px;
}

.main-nav {
  overflow: scroll;
  scrollbar-width: none;
}
.main-nav::-webkit-scrollbar {
  display: none;
}

.main-nav__list {
  position: relative;
  display: flex;
  flex-direction: row;
  margin: 0;
  padding: 0;
  list-style: none;
}

.main-nav__link {
  color: #ffffff;
  transition: 0.5s ease;
}
.main-nav__link:hover {
  color: #ffc464;
}

.main-nav__item {
  margin-right: 30px;
}

.repair {
  overflow: hidden;
}

.repair__offer {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px 30px 0;
}

.repair__offer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.repair__title {
  margin-bottom: 25px;
}

.repair__sub-title {
  margin: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 24px;
  color: #ffffff;
  letter-spacing: 0.5px;
}

.repair__sub-title-bold {
  color: #ffc464;
}

.repair__btn {
  position: absolute;
  bottom: 35px;
  z-index: 3;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  box-sizing: border-box;
  padding: 18px 50px;
  border: none;
  border-radius: 15px;
  background-color: #000000;
  cursor: pointer;
  transition: 0.5s ease;
}
.repair__btn:hover {
  color: #ffc464;
}

.repair__images {
  position: relative;
  width: 340px;
  height: 313px;
}
.repair__images::after {
  position: absolute;
  right: -20px;
  height: 312px;
  width: 185px;
  content: "";
  background: #a95094 url(../img/graphics.webp) no-repeat center bottom;
  background-size: cover;
}

.repair__image {
  position: absolute;
}
.repair__image--repairman {
  top: -10px;
  right: -173px;
  z-index: 2;
  width: 349px;
}
.repair__image--washer {
  bottom: 5px;
  z-index: 1;
  width: 195px;
}

@media (min-width: 780px) {
  .repair__title,
  .repair__sub-title {
    text-align: center;
  }
  .repair__title {
    font-weight: 800;
    font-size: 67px;
    line-height: 64px;
  }
  .repair__sub-title {
    width: 480px;
    margin-bottom: 35px;
    font-size: 20px;
  }
  .repair__images {
    width: 720px;
    height: 545px;
  }
  .repair__images::after {
    height: 544px;
    width: 365px;
  }
  .repair__image--repairman {
    top: inherit;
    bottom: 0;
    right: 0;
    width: 575px;
  }
  .repair__image--washer {
    bottom: 0;
    width: 344px;
  }
  .repair__btn {
    bottom: 75px;
  }
}
@media (min-width: 1440px) {
  .repair__wrapper {
    position: relative;
    width: 1440px;
    margin: 0 auto;
    padding: 30px 135px 0;
    box-sizing: border-box;
  }
  .repair__offer {
    flex-direction: row;
    padding: 0;
  }
  .repair__offer-container {
    position: absolute;
    right: 0;
    top: 170px;
    width: 664px;
    z-index: 5;
    align-items: flex-start;
  }
  .repair__title,
  .repair__sub-title {
    text-align: left;
  }
  .repair__btn {
    position: initial;
  }
  .repair__images {
    width: 781px;
    height: 741px;
  }
  .repair__images::after {
    right: 0;
    left: 67px;
    width: 449px;
    height: 738px;
  }
  .repair__image--repairman {
    width: 781px;
  }
  .repair__image--washer {
    left: -108px;
    bottom: 25px;
    width: 418px;
  }
}
.discount {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px 0 25px;
  background-color: #000000;
}

.discount__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.discount__close-btn {
  position: absolute;
  top: 19px;
  left: 15px;
  width: 14px;
  height: 14px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.discount__close-btn::after, .discount__close-btn::before {
  position: absolute;
  left: 0;
  width: 100%;
  height: 2px;
  content: "";
  background-color: #ffffff;
}
.discount__close-btn::after {
  transform: rotate(45deg);
}
.discount__close-btn::before {
  transform: rotate(-45deg);
}

.discount__offer {
  position: relative;
  margin: 0 0 20px;
  width: 311px;
  color: #ffffff;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
}
.discount__offer::after {
  position: absolute;
  top: 6px;
  left: -5px;
  width: 10px;
  height: 10px;
  content: "";
  background-color: #78c23e;
  border-radius: 50%;
}
.discount__offer::after {
  animation: blinking 1s infinite ease-in;
}

.discount__title {
  color: #ffc464;
}
.discount__title--tablet {
  color: inherit;
}

.discount__btn {
  position: relative;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #ffc464;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.discount__btn::after {
  position: absolute;
  top: 9px;
  right: -65px;
  width: 50px;
  height: 8px;
  content: "";
  background: url(../img/arrow.webp) no-repeat 0 0;
}
.discount__btn:hover::after {
  animation: arrow-move 1s;
}

@media (min-width: 780px) {
  .discount__title--tablet {
    color: #ffc464;
  }
  .discount__offer {
    width: 400px;
  }
  .discount__offer::after {
    top: 7px;
    left: -15px;
  }
}
@media (min-width: 780px) and (min-width: 1440px) {
  .discount {
    width: 1170px;
    position: absolute;
    bottom: 40px;
    z-index: 5;
    flex-direction: row;
    justify-content: center;
    padding: 15px 0;
  }
  .discount__wrapper {
    flex-direction: row;
  }
  .discount__offer {
    width: 770px;
    margin: 0 40px 0;
  }
}